* {
  margin: 0;
}

.header {
  justify-content: space-between;
  width: 100%;
  transition: all .3s;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  margin-top: 10px;
  margin-left: 50px;
}

.menu {
  background-color: #fff;
  margin-right: 50px;
}

.menu__list {
  justify-items: left;
  padding: 0;
  list-style: none;
  display: flex;
}

.menu__item {
  text-transform: uppercase;
}

.menu__link {
  text-align: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.menu__link:not(.is-active) {
  color: #060b35;
  margin-left: 20px;
}

.menu__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  color: #00acdc;
  border: 2px solid #00acdc;
  border-radius: 10px;
  width: 90%;
  position: absolute;
  bottom: 0;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  color: #060b35;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  box-sizing: border-box;
  width: 160px;
  height: 134px;
  margin-left: 3px;
}

.card__title {
  height: 36px;
  margin: 0;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card:hover .card__title {
  color: #34568b;
  cursor: pointer;
}

.card__code {
  color: #616070;
  justify-content: space-between;
  margin: 0;
  padding-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  padding-top: 17px;
  display: flex;
}

.card__rate-reviews {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  padding-top: 23px;
  display: flex;
}

.card__price-text {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__btn {
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 0;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: background-color .3s;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  background-size: cover;
  justify-content: start;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

